@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{
    /* border-radius: 3px; */
    margin:10px;
    background: #fafafa;
    padding:0px 10px 0px 10px;
}

.academics{
    border:solid 1px #bdb8ad;
    /* background-color: #ebe7e0; */
    box-shadow:5px 5px 0px #5e5e5e;
}

.work{
    border:solid 1px #bdbdbd;
    background-color: #f5f5f5;   
    box-shadow: 5px 5px 0px #5e5e5e;
}

.project{
    grid-column: auto;
    border: solid 1px #636d97;
    background-color: #d1d0de;
    box-shadow:5px 5px 0px #5e5e5e;
}

.title-text{
    font-family: 'Playfair Display', serif;
}

.text-accent{
    color: darkslategray;
}


#Intro{
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    background-color: lightsalmon;
}

.margins{
    margin:10px;
}

.paddings{
    padding:10px;
}

.header1{
    font-family: 'Courier Prime', monospace;
    font-weight:bold;
    font-size:1.5em;
}

.header2{
    font-family: 'Courier Prime', monospace;
    font-style: italic; 
    font-weight: bold;
    font-size: 1.2em;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
  
.column {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
}

.grid-flex{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-column-gap: 6px;
    grid-row-gap:6px;
}


.sticky-header{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    background:darksalmon;
    box-shadow: 0px 2px 5px #5e5e5e;
}
#header > div > div:nth-child(2){
    text-align: center;
    line-height: 100%;
}
#header > div > div:nth-child(2) > img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    border-radius: 50%;
}

#header{
    width:100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

